import React,{useState} from 'react';
import {BrowserRouter as Router,Route,Redirect,Switch} from 'react-router-dom'
import './App.css';
import ContextAppProvider from './context/ContextApp';
import MainPageUser from './components/User/MainPageUser';
import Register from './components/Register';
import Login from './components/Login';
import AdminPage from './components/Admin/AdminPage';
import io from 'socket.io-client';

function App() {
  
  const socket = io('http://localhost:3000');

  const [mySocket, setmySocket] = useState(socket)

  return (

    <Router>

    <ContextAppProvider>
    <div className="App">
      <Switch>
      <Route path="/login" component={(props)=><Login hendle={props} />}/>
      <Route path="/register" component={(props)=><Register hendle={props} />}/>
      <Route  path="/main/user" component={(props)=><MainPageUser hendle={props} socket={mySocket}  />}/>
      <Route  path="/admin" component={(props)=><AdminPage hendle={props}  />}/>
      <Redirect from="/" to = '/login'/>
      </Switch>
    </div>
          </ContextAppProvider>
          
    </Router>

  );
}

export default App;
