import React,{useState} from 'react'

export default function Register(props) {

    // register state
    const [firstName, setFirstName] = useState('')
    const [LastName, setLastName] = useState('')
    const [UserName, setUserName] = useState('')
    const [password_r, setPassword_r] = useState('')
    // new user Details
    let userRegister = {firstName,LastName,UserName,password_r}

// hendle register----------------------------------------------->
const hendleRegister = async(e) =>{
    e.preventDefault()
    let data = await fetch('http://localhost:3000/users/register',{
            method:"POST",
            headers:{'content-Type':'application/json'},
            body:JSON.stringify(userRegister)
    })
    setFirstName('')
    setLastName('')
    setPassword_r('')
    setUserName('')
    if(data.status === 200){
        alert('welcome')
        props.hendle.history.push('/login')
    }else if(data.status === 400){
         alert('Oops, that UserName already exists in the system, Please try another one')
    }else{
        alert('something got worng,plz try again')
    }
}
    
    return (
        <div className="register_component container-fluid">
        <h1 className="col-xl-12">Welcome to our Vacation Site,for Register Fill out the form, for Login Click the Login Button</h1>
        <hr/>

        <form className="col-xl-12" onSubmit={hendleRegister}>
                <input onChange={(e)=>setFirstName(e.target.value)} type="text" placeholder='first name' value={firstName} required/>
                <input onChange={(e)=>setLastName(e.target.value)} type="text" placeholder='last name' value={LastName} required/>
                <input onChange={(e)=>setUserName(e.target.value)} type="text" placeholder='user name' value={UserName} required />
                <input onChange={(e)=>setPassword_r(e.target.value)} type="password" placeholder="Password" value={password_r} required/>
                <input className="btn-outline-warning" type="submit" value='Register'/>
            </form>

        </div>
    )
}
