import React,{useContext} from 'react'
import {ContextApp} from '../../context/ContextApp'
import CardFollow from './CardFollow';
import CardUnFollow from './CardUnFollow';

export default function ListVicationUser(props) {

 const {spiner,Follow,UnFollow} = useContext(ContextApp)

    
return (

        <div className="main_box_user row my_row">

            {/* spinner */}
            {spiner?(<div className="spinner-grow" role="status">
              <span className="sr-only">Loading...</span>
            </div>):(null)}

             <h1 className="title_user_box col-xl-12">Welcome to our vication site</h1>
            <div className="follow_vication container-fluid">
            <div className="row my_row">
             <h2 className="col-xl-12">Vacations in your follow up</h2>

             {Follow.length? (null):(<p className="col-xl-12">There are currently no follow-up vacations</p>)}
               {Follow.map(fol =>{
               return(<CardFollow key={fol.vicationID} fol={fol} hendle={props.hendle} socket={props.socket} />)
               })} 
                 </div>
                 </div>

<br/>
<br/>
            <div className="un_follow_vication container-fluid">
                <div className="row my_row">

             <h1 className="col-xl-12">Rest of the Vications you don't follow</h1>
             {UnFollow.length? (null):(<p className="col-xl-12">There are currently no vications, you follow everyone !:)</p>)}

            {UnFollow.map(un =>{
                return(<CardUnFollow fol={un} key={un.vicationID} hendle={props.hendle} socket={props.socket}/>)
            })}
                </div>
            </div>
        </div>
    )
}
