import React,{createContext,useState} from 'react'
import history from './history';

export const ContextApp = createContext()

const ContextAppProvider = (props) =>{

const [allVication, setallVication] = useState([])
const [Follow, setFollow] = useState([])
const [FollowID, setFollowID] = useState([])
const [UnFollow, setUnFollow] = useState([])
const [name, setname] = useState()
const [spiner, setspiner] = useState(false)
    
const hendlespinerFalse = () =>{
    setspiner(false)
}

const hendlespinerTrue = () =>{
    setspiner(true)
}

// set name of user
const hendleName = (name) =>{
    setname(name)
}

// get foloow vication of user--------------------------------------------------------->
const hendleFollow = async() =>{
    console.log('start follow')
        hendlespinerTrue()
        let data = await fetch(`http://localhost:3000/users/vication/get-follow`,{
            method:'GET',
            headers:{'content-Type':'application/json'},
            credentials:'include'
        })
        if(data.status !== 500){
            let arrayData = await data.json()
            setFollow(arrayData)
            for(let i=0;i<arrayData.length;i++){
               FollowID.push(arrayData[i].vicationID)
            }
            // hendle un-follow------------------------------->
            hendleUnFollow()
        }else{
            history.push('/login')
        }
}

// get un-foloow vication of user--------------------------------------------------------->
const hendleUnFollow = async() =>{
        if(FollowID.length === 0){
            hendlespinerFalse()
            // get vication for user that not follow any vication
            let data = await fetch(`http://localhost:3000/users/vication/get-un/0`,{
                method:'GET',
                headers:{'content-Type':'application/json'},
                credentials:'include'    
            })
            if(data.status !== 500){
            let jsonUnVication = await data.json()
            setUnFollow(jsonUnVication)
            }else{
                history.push('/login')
            }   
        }else{
            // get vication for user that follow sum vication
            hendlespinerFalse()
            let data = await fetch(`http://localhost:3000/users/vication/get-un/${FollowID}`,{
                method:'GET',
                headers:{'content-Type':'application/json'},
                credentials:'include'
            })
            if(data.status !== 500){
                let jsonUnVication = await data.json()
                setUnFollow(jsonUnVication)
                FollowID.splice(0,FollowID.length)
            }else{
                 history.push('/login')
            }
        }
}

// get all vication for admin--------------------------------------------------------->
const hendleGetAllAdmin = async() => {
        hendlespinerTrue()
        let data = await fetch('http://localhost:3000/admin/vication',{
            method:'GET',
            headers:{'content-Type':'application/json'},
            credentials:'include'
        })
        if(data.status !== 500){
            let jsonAll = await data.json()
            hendlespinerFalse()
            setallVication(jsonAll)
        }else{
            history.push('/login')
        }
}

return(
     <ContextApp.Provider value={{hendlespinerTrue,hendlespinerFalse,spiner,hendleFollow,Follow,hendleUnFollow,UnFollow,FollowID,hendleName,name,hendleGetAllAdmin,allVication}}>
         {props.children} 
     </ContextApp.Provider> 
)

}

export default ContextAppProvider