import React,{useContext,useEffect,useState} from 'react'
import {BrowserRouter as Router,Route,Redirect,Switch} from 'react-router-dom'
import {NavLink} from 'react-router-dom'
import {ContextApp} from '../../context/ContextApp'
import VicationListAdmin from './VicationListAdmin'
import Chart from './Chart'
import io from 'socket.io-client';
import LightSpeed from 'react-reveal/LightSpeed';

export default function AdminPage(props) {

  const socket = io('http://localhost:3000');
  const [mySocket, setmySocket] = useState(socket)

  const {name,hendleGetAllAdmin,spiner,hendleName} = useContext(ContextApp)
 
 // get all vacation-------------------------->
useEffect(() => {
   hendleVerifyAdmin()
},[])

// socket.io-------------------------------------------------------------------------->
mySocket.on('user-change',()=>{
      hendleGetAllAdmin()
})

// cheack if admin is logedin or need to logein------------------------------------------->
const hendleVerifyAdmin = async()=>{
  let data = await fetch(`http://localhost:3000/admin/verify`,{
    method:'POST',
    headers:{'content-Type':'application/json'},
    credentials:'include',
    body:''
 })
 if(data.status === 500){
  props.hendle.history.push('/')
 }else{
   let jsonData = await data.json()
   hendleName(jsonData[1].user)
   hendleGetAllAdmin()
 }

}

// user LogOut and delete TOKEN-------------------------------------------------------->
const hendleLogOut = async() =>{
    let data = await fetch(`http://localhost:3000/admin/logOut`,{
      method:'DELETE',
      headers:{'content-Type':'application/json'},
      credentials:'include',
      body:''
   })
   console.log(data)
   if(data.status === 200){
     props.hendle.history.push('/')
   }else{
     alert('plz try again')
   }
  
}
  
    return (
<Router>
<div className="main_admin container-fluid">

<div className="navbar_admin row my_row">
<LightSpeed left>

<h4 className="title_admin col-xl-8">Hi {name}, We're glad you're here</h4>

<div className="div_navlink col-xl-2">
           <NavLink  to="/admin/list">
            <button className="btn-outline-secondary"> <i className="fas fa-globe-europe"></i>
            list
            </button> 
           </NavLink> 
</div>

<div className="div_navlink col-xl-2">
           <NavLink to="/admin/chart">
             <button className="btn-outline-secondary"><i className="fas fa-chart-line"></i>
             chart
             </button> 
           </NavLink> 
</div>

        <div className="div_navlink col-xl-2">
        <button className="btn-outline-secondary" onClick={hendleLogOut}><i className="fas fa-sign-out-alt"></i>LogOut</button>
        </div>
        </LightSpeed>
        </div>

  <div className="components_admin ">
            {/* spinner */}
            {spiner?(<div className="spinner-grow" role="status">
              <span className="sr-only">Loading...</span>
            </div>):(null)}

    <Switch>
        <Route path="/admin/list" component={()=><VicationListAdmin hendle={props.hendle} socket={mySocket} />}/>
        <Route path="/admin/chart" component={()=><Chart socket={mySocket} />}/>
        <Redirect to ='/admin/list'/>
    </Switch>
  </div>
</div>
 </Router>

    )
}
