import React,{useState,useEffect} from 'react'
import {NavLink} from 'react-router-dom'

export default function Login(props) {

// login state
const [user, setUser] = useState('')
const [password, setPassword] = useState('')
let userLogin = {user,password}

useEffect(() => {
    hendleVerifyForAll()
}, [])

// chack if someone conect--------------------------------------->
const hendleVerifyForAll = async()=>{
    // cheack if user is logedin or need to logein------------------------------------------->
    console.log('start verify')
    let data = await fetch(`http://localhost:3000/users/verify`,{
      method:'POST',
      headers:{'content-Type':'application/json'},
      credentials:'include',
      body:''
   })
   if(data.status !== 500){
     console.log('worng token')
     props.hendle.history.push('/main/user')
   }else{
       // cheack if admin is logedin or need to logein------------------------------------------->
    let data = await fetch(`http://localhost:3000/admin/verify`,{
      method:'POST',
      headers:{'content-Type':'application/json'},
      credentials:'include',
      body:''
   })
   if(data.status !== 500){
    props.hendle.history.push('/admin')
    console.log('worng token admin/verify')
   }
  }
   }

// hendle Login-------------------------------------------------->
const hendleLogin = async(e) =>{
        e.preventDefault()
     let data = await fetch('http://localhost:3000/users/Login',{
        method:"POST",
        headers:{'content-Type':'application/json'},
        credentials:'include',
        body:JSON.stringify(userLogin)
     })
     setUser('')
     setPassword('') 
     if(data.status === 422){
        alert('One or more of the details is incorrect') 
     }else{
     let newData = await data.json()
     if(newData[2].admin === false && data.status === 200){
         alert('welcome')
         props.hendle.history.push('/main/user')
     }else if(newData[2].admin === true && data.status === 200){
         props.hendle.history.push('/admin')
    }else if(data.status === 500){
        alert('opss,something got worng,plz try again')
    }
}
}
  
    return (
        <div className="login_component container-fluid">
            <div className="row my_row">
            <h1 className="col-xl-12">Welcome to our Vacation Site,for login Fill out this form. </h1>          
            <hr/>

            <form className="col-xl-12" onSubmit={hendleLogin}>
             <input onChange={(e)=>setUser(e.target.value)} type="text" placeholder="User name" value={user} required/>
             <input onChange={(e)=>setPassword(e.target.value)}type="password" placeholder="Password" value={password} required/>
             <input className="btn-outline-success" type="submit" value="Login"/>
           </form>   
           
<NavLink to="/register" className="nav">If you haven't signed up to us yet, please sign up </NavLink>
          
            </div>
        </div>
    )
}
