import React,{useContext,useEffect,useState} from 'react'
import ListVicationUser from './ListVicationUser'
import {ContextApp} from '../../context/ContextApp'
import LightSpeed from 'react-reveal/LightSpeed';

export default function MainPage(props) {
    
  const {name,hendleName,hendleFollow} = useContext(ContextApp)

  useEffect(() => {
    hendleVerify()
  },[])


props.socket.on('admin-change',()=>{
    hendleFollow()
})    

// cheack if user is logedin or need to logein
const hendleVerify = async()=>{
  console.log('start verify')
  let data = await fetch(`http://localhost:3000/users/verify`,{
    method:'POST',
    headers:{'content-Type':'application/json'},
    credentials:'include',
    body:''
 })
 if(data.status === 500){
   props.hendle.history.push('/login')
 }else{
   let jsonData = await data.json()
   hendleName(jsonData[1].user)
   hendleFollow()
 }
}

// user LogOut and delete TOKEN-------------------------------------------------------->
const hendleLogOut = async() =>{
  let data = await fetch(`http://localhost:3000/users/logOut`,{
    method:'DELETE',
    headers:{'content-Type':'application/json'},
    credentials:'include',
    body:''
 })
 if(data.status === 200){
   props.hendle.history.push('/')
 }else{
   alert('plz try again')
 }
}

    return (
        <div className="main_user_component container-fluid">
        <div className="navbar_user row my_row">
          <LightSpeed>
             <h3 className="title_user col-xl-9">Hi {name}, We're glad you're here</h3>
             <button className="log_out_btn btn-outline-secondary col-xl-3" onClick={hendleLogOut}><i className="fas fa-sign-out-alt"></i>LogOut</button>
          </LightSpeed>
             </div>

          <ListVicationUser hendle={props.hendle} socket={props.socket}  />
        </div>
    )
}
