import React,{useContext,useState} from 'react'
import {ContextApp} from '../../context/ContextApp'
import Swing from 'react-reveal/Swing';
import Fade from 'react-reveal/Fade';
import Moment from 'react-moment'

export default function VicationListAdmin(props) {

    const {hendleGetAllAdmin,allVication} = useContext(ContextApp)
    // check modal for add vication
    const [btnAdd, setbtnAdd] = useState(false)
    // chack modal for update vication
    const [btnUpdate, setbtnUpdate] = useState(false)
    const [vicID, setVicID] = useState()
    // get info of vication before update
    const [Destination1, setDestination1] = useState('')
    const [Description1, setDescription1] = useState('')
    const [Image1, setImage1] = useState('')
    const [start_date1, setStart_date1] = useState('')
    const [end_date1, setEnd_date1] = useState('')
    const [Price1, setPrice1] = useState('')
    // state for form/add vication
    const [Destination, setDestination] = useState('')
    const [Description, setDescription] = useState('')
    const [Image, setImage] = useState('')
    const [start_date, setStart_date] = useState('')
    const [end_date, setEnd_date] = useState('')
    const [Price, setPrice] = useState('')
    
    let newVication = {Destination,Description,Image,start_date,end_date,Price}
    let updateVication = {Destination1,Description1,Image1,start_date1,end_date1,Price1}


// delete vication by admin------------------------------------------------------->
const hendleDelete = async(e) =>{
    
    let data = await fetch(`http://localhost:3000/admin/delete/${e.target.id}`,{
        method:"DELETE",
        headers:{'content-Type':'application/json'},
        credentials:'include',
        body:''
    })
    if(data.status === 200){
        hendleGetAllAdmin()
        props.socket.emit('admin-change')
    }else if(data.status === 500){
        props.hendle.history.push('/login')
    }else{
        alert('something got worng,plz try again')
    }
}


// add vication by admin--------------------------------------------------------->
const hendleAdd = async(e) =>{
    e.preventDefault()
    let data = await fetch('http://localhost:3000/admin/add',{
        method:"POST",
        headers:{'content-Type':'application/json'},
        credentials:'include',
        body:JSON.stringify(newVication)
    })
    setDestination('')
    setDescription('')
    setImage('')
    setStart_date('')
    setEnd_date('')
    setPrice('')
    setbtnAdd(!btnAdd)
    if(data.status === 200){
                props.socket.emit('admin-change')
                 alert('The flight was added')
                 hendleGetAllAdmin()   
         }else if(data.status === 500){
             props.hendle.history.push('/login')
        }
        else{
            alert('something got worng,plz try again')       
     } 
}

// update vication by admin-------------------------------------------------------->
const hendleUpdate = async(e) =>{
    e.preventDefault()
    let data = await fetch(`http://localhost:3000/admin/update/${e.target.id}`,{
        method:"PUT",
        headers:{'content-Type':'application/json'},
        credentials:'include',
        body:JSON.stringify(updateVication)
    })
    setDestination('')
    setDescription('')
    setImage('')
    setStart_date('')
    setEnd_date('')
    setPrice('')
    setbtnUpdate(!btnUpdate)
    if(data.status === 200){
        props.socket.emit('admin-change')
        alert('The flight was change')
        hendleGetAllAdmin()
    }else if(data.status === 500){
        props.hendle.history.push('/login')
    }else{
        alert('something got worng,plz try again')
    }
}

    return (
        <div className="row my_row">
        <Swing>
             <button onClick={()=>setbtnAdd(!btnAdd)} className="btn_add  btn-outline-success"><i className="fas fa-plus-square"></i>Add Vication</button>
        </Swing>
             {btnAdd?(
                 <div className="boxForm">
                 <div className="form_add ">
                 {/* form add */}
                 <Fade right>

                 <form  onSubmit={hendleAdd}>
                     <button className="btn_x1 btn-outline-danger" onClick={()=>setbtnAdd(!btnAdd)}><i className="fas fa-times"></i></button>
                     <input onChange={(e)=>setDestination(e.target.value)} type="text" placeholder="Destination" value={Destination} required/>
                     <input onChange={(e)=>setDescription(e.target.value)} type="text" placeholder="Description" value={Description} required/>
                     <input onChange={(e)=>setImage(e.target.value)} placeholder="copy link of image"  type="text" value={Image} required/>
                     <div className="inputDateUpdate"> Flight date: <input onChange={(e)=>setStart_date(e.target.value)} type="date"  value={start_date} required/></div>
                     <div className="inputDateUpdate"> Return Flight Date: <input onChange={(e)=>setEnd_date(e.target.value)} type="date"  value={end_date} required/></div>
                     <input onChange={(e)=>setPrice(e.target.value)} type="number" placeholder="price" value={Price} required/>
                     <input className="btn-outline-success" type="submit" value="Add Vication"/>
                  </form>
                  </Fade>
                 </div>
                 </div>):(null)}
         <div className="row my_row">
             {allVication.map(all =>{
                return(<div key={all.vicationID} id={all.vicationID} className="card col-xl-3">
                    <button className="btn_u btn-outline-warning col-xl-2" id={all.vicationID} onClick={(e)=>{ setbtnUpdate(!btnUpdate)
                                                                setVicID(e.target.id)
                                                                {/* set info for form */}
                                                                setDestination1(all.destination)
                                                                setDescription1(all.Description)
                                                                setImage1(all.Image)
                                                                setStart_date1(all.start_date)
                                                                setEnd_date1(all.end_date)
                                                                setPrice1(all.price)

                        }}><i className="fas fa-pen-alt"></i></button>
                    <button className="btn_x btn-outline-danger col-xl-2" id={all.vicationID} onClick={hendleDelete}><i id={all.vicationID} className="fas fa-times"></i></button>
                    <div className="card-body">
                        <img src={all.Image} className="card-img-top" alt="..."/>
                    <h5 className="card-title col-xl-12">{all.destination}</h5>
                    <p className="card-text col-xl-12">{all.Description}</p>
                    </div>
                    <ul className="list-group list-group-flush">
                      <li className="list-group-item col-xl-12"><strong>Flight forth</strong>: <Moment format="DD/MM/YYYY">{all.start_date}</Moment></li>
                      <li className="list-group-item col-xl-12"><strong>Flight back</strong>: <Moment format="DD/MM/YYYY">{all.end_date}</Moment></li>
                      <li className="list-group-item col-xl-12"><strong>price</strong>: {all.price} $</li>
                    </ul>
                    </div>)
            })}

            {btnUpdate?(
                <div className="boxForm">
                        <div className="form_update">
                        <Fade left>

                            {/* form update */}
                            <button className="btn_x1 btn-outline-danger" onClick={()=>setbtnUpdate(!btnUpdate)} ><i className="fas fa-times"></i></button>
                        <form id={vicID} onSubmit={hendleUpdate}>
                     <input onChange={(e)=>setDestination1(e.target.value)} type="text" placeholder="Destination" value={Destination1} />
                     <input onChange={(e)=>setDescription1(e.target.value)} type="text" placeholder="Description" value={Description1} />
                     <input onChange={(e)=>setImage1(e.target.value)} type="text" src={Image1} placeholder="Image" value={Image1}/>
                    <div className="inputDateUpdate">Flight date: <input onChange={(e)=>setStart_date1(e.target.value)} type="date" value={start_date1} required/> (<Moment format="DD/MM/YYYY">{start_date1}</Moment>)</div> 
                     <div className="inputDateUpdate">Return Flight Date: <input onChange={(e)=>setEnd_date1(e.target.value)} type="date" value={end_date1} required/> (<Moment format="DD/MM/YYYY">{end_date1}</Moment>)</div>
                     <input onChange={(e)=>setPrice1(e.target.value)} type="number" placeholder="price" value={Price1} />
                     <input className="btn-outline-success" type="submit" value="Update Vication"/>
                 </form>
                 </Fade>
                        </div>
                        </div>
                    ):(null)}

         </div>
        </div>

    )
}
