import React,{useState,useContext,useEffect} from 'react'
import {ContextApp} from '../../context/ContextApp'
import {Bar} from 'react-chartjs-2'

export default function Chart(props) {

const {allVication} = useContext(ContextApp)
    
// numbers followers of vication
const [info, setinfo] = useState([])
//random color
const [color, setcolor] = useState([])    

// to get array of the number for each vication=============================>
useEffect(() => {
    getInfoForChart()
    },[])

// funcation get info---------------------------------------------------------->
const getInfoForChart = () => {
    {allVication.map(all=>{
    info.push(all.followers)
    let colorNew = "#"+((1<<24)*Math.random()|0).toString(16)
    color.push(colorNew)
    })}
        let newArray = info.concat(info)
        setinfo([newArray])
}

// state of chart-------------------------------------------------------------->
const [chartData, setchartData] = useState({
    labels:allVication.map(all =>{
        return (`'${all.destination},'`)
        }),
        datasets:[
            {
          label:'DataBase-followers',
          fill: false,
          borderWidth: 2,
          backgroundColor :color,
          data:info
     }]
 }
 )


    return (
        <div className="chart">
          <Bar 
options={{
  title:{
   display:true,
   text:'Vication Followers',
   fontSize:20
  },
  responsive : true,
  maintainAspectRatio:false,
  scales: {
            yAxes: [{
                ticks: {
                    beginAtZero:true
                }
            }]
        }
}}

data={chartData}
/>

        </div>
    )
}
