import React,{useContext} from 'react'
import {ContextApp} from '../../context/ContextApp'
import Moment from 'react-moment'

export default function CardFollow(props) {

    const {hendleFollow,iduser} = useContext(ContextApp)

// delete follow for user on vication--------------------------------------------------->
const hendleDeleteFollow = async(e)=>{
    let data = await fetch(`http://localhost:3000/users/vication/un-follow/${e.target.id}`,{
            method:'POST',
            headers:{'content-Type':'application/json'},
            credentials:'include',
            body:''
        })
        if(data.status === 200){
            // ok
            props.socket.emit('user-change')
            hendleFollow(iduser)
         }else if(data.status === 500){
             props.hendle.history.push('/')
         }else{
            //  err
             alert('something got worng, plz try again')
         }
}

    return (
          
                    <div key={props.fol.vicationID} className="card col-xl-3" >
                    <img src={props.fol.Image} className="card-img-top" alt="..."/>
                    <div className="card-body">
                    <h5 className="card-title col-xl-12">{props.fol.destination}</h5>
                    <p className="card-text col-xl-12"> {props.fol.Description}</p>
                    </div>
                    <ul className="list-group list-group-flush">
                      <li className="list-group-item col-xl-12"><strong>Flight forth</strong> : <Moment format="DD/MM/YYYY">{props.fol.start_date}</Moment></li>
                      <li className="list-group-item col-xl-12"><strong>Flight back</strong> : <Moment format="DD/MM/YYYY">{props.fol.end_date}</Moment></li>
                      <li className="list-group-item col-xl-12"><strong>price</strong> : {props.fol.price} $</li>
                    </ul>
                <button className="btn-outline-secondary" id={props.fol.vicationID} onClick={hendleDeleteFollow}><i className="fas fa-heart-broken"></i> Unfollow</button>
                 </div>
  
    )
}
